import React from "react";
import { screen, render, fireEvent } from "@testing-library/react";
import PaymentScreen from "./../../PaymentScreen";
import "@testing-library/jest-dom/extend-expect";
import { Provider } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import { defineFeature, loadFeature } from "jest-cucumber";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  MemoryRouter,
  useLocation,
} from "react-router-dom";
import parkingSlice from "../../redux/reducer/parkingSlice";
let store: any;
const feature = loadFeature("src/components/features/paymentscreen.feature");
defineFeature(feature, (test) => {
  test("User pays for occupied parking spot", ({ given, then, when, and }) => {
    jest.mock("react-router", () => ({
      ...(jest.requireActual("react-router") as {}),
      useLocation: jest.fn().mockImplementation(() => {
        return { pathname: "/payment" };
      }),
    }));
    const spaceDetails = {
      registration: "ABC",
      parkingTime: "14:00",
    };
    const space = "1";
    const mockLocation = {
      state: {
        spaceDetails,
        space,
      },
    };
    given("I am on the Payment Screen", () => {
      store = configureStore({
        reducer: {
          parkingSpaces: parkingSlice,
        },
      });
      const state = {
        spaceDetails: {
          registration: "ABC123",
          parkingTime: "08:30",
        },
        space: 1,
      };
      jest
        .spyOn(require("react-router-dom"), "useLocation")
        .mockReturnValue(mockLocation);
      render(
        <Provider store={store}>
          <MemoryRouter initialEntries={["/payment"]}>
            <Routes>
              <Route path="/payment" element={<PaymentScreen />} />
            </Routes>
          </MemoryRouter>
        </Provider>
      );
    });

    and(
      "I see the vehicle registration and hours parked for the occupied parking spot",
      async () => {
        store = configureStore({
          reducer: {
            parkingSpaces: parkingSlice,
          },
        });
        const spaceDetails = { registration: "ABC", parkingTime: "12:30" };
        const space = 1;
        jest
          .spyOn(require("react-router-dom"), "useLocation")
          .mockReturnValue(mockLocation);

        const { getByTestId } = render(
          <Provider store={store}>
            <MemoryRouter initialEntries={[`/payment`]}>
              <Route path="/payment">
                <PaymentScreen />
              </Route>
            </MemoryRouter>
          </Provider>
        );
        expect(await screen.findByTestId("reg")).toHaveTextContent(
          "Vehicle Registration: ABC123"
        );
      }
    );
  });
});
